@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Comfortaa", cursive, sans-serif;
}

.items-cards {
    background: #ecf0f3;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
}


/* modal */

.modal {
    position: absolute;
    left: 0;
    top: 50%;
    z-index: 1;
    display: flex;
    margin: auto;
    height: 400px;
    width: 400px;
    background-color: red;
}
.card {
    width: 270px;
    height: 370px;
    text-align: center;
    margin: 20px 20px;
    padding: 10px;
    background: #ecf0f3;
    border-radius: 10px;
    box-shadow: 8px 8px 8px #d1cbcb, -8px -8px 8px #ffffff;
    transition-duration: 0.5s;
}

img {
    height: 140px;
    margin: 15px;
    -webkit-filter: saturate(1.2) brightness(1.1);
            filter: saturate(1.2) brightness(1.1);
    transition-duration: 0.5s;
}

.card:hover {
    /* background: #fbfcfd; */
    transform: scale(1.03);
    transition-duration: 0.2s;
}

.card:hover img {
    transform: scale(1.4);
    transition-duration: 0.5s;
}

.card p:nth-child(1) {
    text-transform: uppercase;
}

.card p:nth-child(2),
.card p:nth-child(3),
.card p:nth-child(4) {
    text-transform: capitalize;
    font-weight: 800;
}

.card p:nth-child(5) {
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    color: #666666;
}

.card p:nth-child(5),
.card p:nth-child(6) {
    display: inline-block;
}

p {
    /* z-index: 500; */
    /* transform: scaleZ(4); */
    margin: 5px;
    letter-spacing: 2px;
}


/* 



button
*/

.btn-shop {
    cursor: pointer;
    margin: 15px;
    padding: 10px;
    width: 80%;
    color: #fff;
    background: #000;
}

.btn-shop:hover {
    color: #000;
    background: #fff;
}

.btn-shop:active {
    background: #c2c2c2;
}
